import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import BAR101 from '../views/BAR101.vue'
import BAR102 from '../views/BAR102.vue'
import Upload from '../views/UploadDropImage.vue'
import ListInterventions from '../views/ListIntervention.vue'
import NouvelleIntervention from '../views/NouvelleIntervention.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/BAR101',
    name: 'BAR101',
    component: BAR101
  },
  {
    path: '/BAR102ITI',
    name: 'BAR102ITI',
    component: BAR102
  },
  {
    path: '/BAR102ITE',
    name: 'BAR102ITE',
    component: BAR102
  },
  {
    path: '/Upload',
    name: 'Upload',
    component: Upload
  },
  {
    path: '/NouvelleIntervention',
    name: 'NouvelleIntervention',
    component: NouvelleIntervention
  },
  {
    path: '/Interventions',
    name: 'Interventions',
    component: ListInterventions
  },
]

const router = new VueRouter({
  routes
})

export default router
