/* Definition du composant du information client */
<template>
        <v-row>
            <v-col cols="12" sm="6" md="4">
                <v-text-field
                label="Nom*"
                required
                dense
                ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
                <v-text-field
                label="Prnom"
                hint=""
                dense
                ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
             
                
                 
                 <v-menu
                  v-model="menu2"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
                >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="computedDateFormatted"
              hint="Date du rapport"
             dense
              persistent-hint
              prepend-icon="mdi-calendar"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date"
            no-title
             :first-day-of-week="1"
      locale="fr"
            @input="menu2 = false"
          ></v-date-picker>
        </v-menu>
            </v-col>
            <v-col cols="12">
                <v-text-field
                label="Adresse du client*"
                required
                dense
                ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6">
                <v-text-field
                label="Code Postal*"
                type="codePostal"
                required
                dense
                ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6">
                <v-text-field
                label="Ville*"
                type="ville"
                required
                dense
                ></v-text-field>
            </v-col>
            <!-- <v-col cols="12" sm="6">
                <v-select
                :items="['0-17', '18-29', '30-54', '54+']"
                label="Age*"
                required
                dense
                ></v-select>
            </v-col>
            <v-col cols="12" sm="6">
                <v-autocomplete
                :items="[
                    'Skiing',
                    'Ice hockey',
                    'Soccer',
                    'Basketball',
                    'Hockey',
                    'Reading',
                    'Writing',
                    'Coding',
                    'Basejump',
                ]"
                label="Interests"
                multiple
                dense
                ></v-autocomplete>
            </v-col> -->
        </v-row>
</template>

<script>

  export default {
    name: 'InfoClient',

    data: vm => ({
      date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
      dateFormatted: vm.formatDate((new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10)),
      menu1: false,
      menu2: false,
      val:null
    }),

    computed: {
      computedDateFormatted () {
        return this.formatDate(this.date)
      },
    },

    // watch: {
    //   date (val) {
    //     this.dateFormatted = this.formatDate(this.date)
    //   },
    // },

    methods: {
      formatDate (date) {
        if (!date) return null

        const [year, month, day] = date.split('-')
        return `${day}/${month}/${year}`
      },
      parseDate (date) {
        if (!date) return null

        const [month, day, year] = date.split('/')
        return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
      },
    },
  
    

  }
</script>

